import { App } from "@tinyhttp/app";
import * as express from "express";
import * as path from "path";
const app = new App();
const staticServer: any = express.static("build");
app.use(staticServer);

app.get("/", (req, res) => {
  res.sendFile(path.join(__dirname, "public", "index.html"));
});

app.listen(3001, () => {
  console.log("Server");
});
